<template>
<div class="page page-current" v-if="!success">
    <header class="bar bar-nav">
        <router-link to="/uc/coin" class="button button-link button-nav pull-left"><span class="icon icon-left"></span></router-link>
        <h1 class="title"></h1>
    </header>
    <div class="content gray">
        <div class="description wihte">
            <br> 1
            <br> 21 5</div>
        <div class="main-box mt10">
            <div class="box-bd">
                <div class="list-block">
                    <ul class="thinForm">
                        <li>
                            <div class="item-content item-money">
                                <div class="item-inner">
                                    <div class="item-title label"></div>
                                    <span class="unit"></span>
                                    <div class="item-input">
                                        <el-input v-model="form.totalFee" placeholder="" autofocus></el-input>
                                    </div>
                                </div>
                            </div>
                        </li>
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-title label"></div>
                                    <div class="item-input">
                                        <el-input v-model="form.wechatName" placeholder="" autofocus></el-input>
                                    </div>
                                </div>
                            </div>
                        </li>
                        <li class="multi">
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-title label"></div>
                                    <div class="item-input">
                                        <el-input v-model="form.remark" type="textarea" :rows="3" placeholder=""></el-input>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
                <div class="submit-wrap">
                    <a href="javascript:;" @click.stop="submitFun" class="button button-big button-fill"></a>
                </div>
            </div>
        </div>
    </div>
</div>
<div class="page page-current" v-else>
    <header class="bar bar-nav">
        <router-link to="/uc/coin" class="button button-link button-nav pull-right"></router-link>
        <h1 class="title"></h1>
    </header>
    <div class="content gray">
        <div class="success-wrap">
            <span class="text"></span>
            <div class="money"><span class="unit"></span><span class="val">{{form.totalFee}}</span></div>
            <div class="detail clearfix">
                <span class="name"></span>
                <span class="val">{{applyId}}</span>
            </div>
        </div>
        <div class="concat-wrap mt10">
            <div class="pf-wrap">
                <div class="img-wrap"> <img src="/static/demo/photo.png" alt=""></div>
                <div class="inner">
                    <p></p>
                    <div class="attr-item">
                        <span class="attr-name"></span>
                        <span class="attr-val">7*24</span>
                    </div>
                </div>
            </div>
            <div class="code-wrap">
                <div class="code-img">
                    <img src="/static/demo/photo.png" alt="">
                </div>
                <div class="sub"></div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
let $ = window.$;
export default {
    name: 'ucPay',
    data() {
        return {
            success: false,
            applyId: '',
            form: {
                exchangeType: 'deposit',
                totalFee: '',
                wechatName: '',
                remark: ''
            }
        };
    },
    methods: {
        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-11
         * @lastTime    2018-06-11
         * @description 
         */
        submitFun() {
            $.showPreloader();
            this.$http.post('/coin/exchange', this.form).then((response) => {
                $.hidePreloader();
                if (response.data.success) {
                    this.success = true;
                    this.applyId = response.data.data.applyId;
                } else {
                    $.alert(response.data.message);
                }
            }).catch(() => {
                $.hidePreloader();
            });
        }
    }
};
</script>



