* @Author: weiberzeng
* @Date:   2018-04-23 20:41:10
* @Last Modified by:   weiberzeng
* @Last Modified time: 2018-06-11 17:16:44
-->
<template>
    <div class="page page-current">
        <div class="content">
            <div class="accountWrap">
                <div class="list-block">
                    <ul>
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-title label"></div>
                                    <div class="item-input">
                                        <el-input v-model="form.phone" maxlength="11" placeholder="" autofocus></el-input>
                                    </div>
                                </div>
                            </div>
                        </li>
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-title label"></div>
                                    <div class="item-input">
                                        <el-input v-model="form.authCode" maxlength="6" placeholder=""></el-input>
                                    </div>
                                    <span @click.stop="sendAuthCodeFun" :class="count===0?'codeBtn':'codeBtn done'">{{authCodeText}}</span>
                                </div>
                            </div>
                        </li>
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-title label"></div>
                                    <div class="item-input">
                                        <el-input v-model="form.password" maxlength="12" type="password" placeholder="6-12"></el-input>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
                <div class="submitWrap">
                    <a href="javascript:;" @click.stop="regFun('business')" class="button button-big button-fill"></a>
                    <a href="javascript:;" @click.stop="regFun('brushhand')" class="button button-big button-light"></a>
                </div>
                <div class="description"></div>
                <div class="linkWrap bottom">
                    <router-link to="/account/login"><span></span></router-link>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import utils from '@/javascript/utils';
let $ = window.$;
export default {
    name: 'accountReg',
    data() {
        return {
            count: 0,
            authCodeText: '',
            form: {
                phone: '',
                password: '',
                authCode: '',
                userRole: ''
            },
            validate: {
                phone: false,
                password: false,
                authCode: false
            }
        };
    },
    watch: {
        'form.phone': 'validatePhone',
        'form.password': 'validatePassword',
        'form.authCode': 'validateAuthCode'
    },
    methods: {
        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-04
         * @lastTime    2018-06-04
         * @description 
         */
        validatePhone(to, from) {
            if (to.length < 11) {
                this.validate.phone = false;
            } else {
                this.validate.phone = (to.length) > 0 && (/^(13[0-9]|15[012356789]|17[0678]|18[0-9]|14[57])[0-9]{8}$/).test(utils.trim(to));
            }
        },
        validateAuthCode(to, from) {
            if (to.length !== 6) {
                this.validate.authCode = false;
            } else {
                this.validate.authCode = true;
            }
        },
        validatePassword(to, from) {
            if (to.length < 6) {
                this.validate.password = false;
            } else if (to.length > 12) {
                this.validate.password = false;
            } else {
                this.validate.password = true;
            }
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-04
         * @lastTime    2018-06-04
         * @description 
         */
        sendAuthCodeFun() {
            if (this.count !== 0) return;
            if (!this.validate.phone) {
                $.toast('');
                return;
            }

            this.count = 60;
            let _that = this;
            let setCount = function() {
                if (_that.count > 0) {
                    _that.count = _that.count - 1;
                    if (_that.count > 0) {
                        _that.authCodeText = _that.count + '';
                    } else {
                        _that.authCodeText = '';
                    }
                    setTimeout(setCount, 1000);
                }
            };
            setCount();

            this.$http.post('/authCode/send', {
                phone: this.form.phone,
                authCodeEvent: 'register'
            }).then((response) => {
                if (response.data.success) {
                    $.toast('');
                } else {
                    $.alert(response.data.message);
                }
            });
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-04
         * @lastTime    2018-06-04
         * @description 
         */
        regFun(type) {
            if (!this.validate.phone) {
                $.toast('');
                return;
            }
            if (!this.validate.authCode) {
                $.toast('6');
                return;
            }
            if (!this.validate.password) {
                $.toast('6-12');
                return;
            }

            let title;
            let text;
            let ok;
            let _that = this;
            if (type === 'brushhand') {
                title = '';
                text = '';
                ok = '';
            }
            if (type === 'business') {
                title = '';
                text = '';
                ok = '';
            }

            $.confirm(text, title, function() {
                _that._regFun(type);
            }, function() {}, ok, '');
        },
        _regFun(type) {
            $.showPreloader();
            this.form.userRole = type;
            this.$http.post('/user/register', this.form).then((response) => {
                $.hidePreloader();
                if (response.data.success) {
                    localStorage.setItem('userInfo', JSON.stringify(response.data.data.user));
                    this.$store.state.userInfo = response.data.data.user;
                    // 
                    if (type === 'brushhand') {
                        this.$router.replace({
                            path: '/home/seller'
                        });
                    }
                    // 
                    if (type === 'business') {
                        this.$router.replace({
                            path: '/home/buyer'
                        });
                    }
                } else {
                    $.alert(response.data.message);
                }
            }).catch(() => {
                $.hidePreloader();
            });
        }
    }
};

</script>



