<template>
    <div class="page page-current">
        <header class="bar bar-nav">
            <router-link to="/uc/default" class="button button-link button-nav pull-left"><span class="icon icon-left"></span></router-link>
            <h1 class="title"></h1>
        </header>
        <div class="content">
            <div class="description">(1)
                <br>(2)
                <br>(3)</div>
            <div class="main-box">
                <div class="box-bd">
                    <div class="list-block">
                        <ul class="thinForm">
                            <li>
                                <div class="item-content">
                                    <div class="item-inner">
                                        <div class="item-title label"></div>
                                        <div class="item-input">
                                            <el-input v-model="form.realName" placeholder="" autofocus></el-input>
                                        </div>
                                    </div>
                                </div>
                            </li>
                            <li>
                                <div class="item-content">
                                    <div class="item-inner">
                                        <div class="item-title label"></div>
                                        <div class="item-input">
                                            <el-input v-model="form.identifyNo" maxlength="18" placeholder=""></el-input>
                                        </div>
                                    </div>
                                </div>
                            </li>
                            <li>
                                <div class="item-content">
                                    <div class="item-inner">
                                        <div class="item-title label"></div>
                                        <div class="item-input">
                                            <el-input v-model="form.taobaoAccount" placeholder=""></el-input>
                                        </div>
                                    </div>
                                </div>
                            </li>
                            <li>
                                <div class="item-content">
                                    <div class="item-inner">
                                        <div class="item-title label"></div>
                                        <div class="item-input">
                                            <el-input v-model="form.alitm" placeholder=""></el-input>
                                        </div>
                                    </div>
                                </div>
                            </li>
                            <li>
                                <div class="item-content">
                                    <div class="item-inner">
                                        <div class="item-title label"></div>
                                        <div class="item-input">
                                            <el-input v-model="form.storeUrl" placeholder=""></el-input>
                                        </div>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            <div class="main-box">
                <div class="box-hd"><span class="text"></span></div>
                <div class="box-bd">
                    <div class="id-upload">
                        <div class="text"></div>
                        <div class="inner">
                            <div class="upload" @click="checkPhotoFun('idPhoto')">
                                <span v-if="uploadResult==='progress'" class="percent">{{progress}}<i>%</i></span>
                                <img :src="imageUrl" alt="">
                                <input type="file" ref="idPhoto" name="multipartFiles" accept="image/*" @change="uploadPhotoFun('idPhoto')">
                            </div>
                        </div>
                    </div>
                </div>
                <div class="submit-wrap">
                    <a href="javascript:;" @click.stop="submitFun" class="button button-big button-fill"></a>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import upload from '@/javascript/upload';
let $ = window.$;
export default {
    name: 'authbuyer',
    data() {
        return {
            imageUrl: '/static/image/id-bg.png',
            uploadResult: 'wait',
            progress: '0',
            form: {
                id: '',
                realName: '',
                identifyNo: '',
                taobaoAccount: '',
                alitm: '',
                storeUrl: '',
                identifyNoUrl: ''
            }
        };
    },
    methods: {
        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-07
         * @lastTime    2018-06-07
         * @description 
         */
        checkPhotoFun(name) {
            if (this.uploadResult === 'wait') {
                this.$refs[name].dispatchEvent(new MouseEvent('click'));
            } else if (this.uploadResult === 'success' || this.uploadResult === 'progress') {
                // 
            } else {
                this.uploadPhotoFun(name);
            }
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-07
         * @lastTime    2018-06-07
         * @description 
         */
        uploadPhotoFun(name) {
            let _that = this;
            // 
            let file = this.$refs[name].files[0];
            // 
            this.imageUrl = URL.createObjectURL(file);
            // 
            upload({
                action: '/douyaer-api/file/upload',
                filename: 'multipartFiles',
                file: file,
                onSuccess(response) {
                    if (response.success) {
                        _that.uploadResult = 'success';
                        _that.form.identifyNoUrl = response.data.visitUrls[0].fileUrl;
                        $.toast('');
                    } else {
                        _that.uploadResult = 'fail';
                        $.toast(response.message);
                    }
                },
                onProgress(progress) {
                    _that.uploadResult = 'progress';
                    _that.progress = progress.percent;
                },
                onError(e) {
                    _that.uploadResult = 'fail';
                    $.toast('');
                }
            });
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-06
         * @lastTime    2018-06-06
         * @description 
         */
        submitFun() {
            $.showPreloader();
            this.$http.post('/cert/businessAddOrUpdate', this.form).then((response) => {
                $.hidePreloader();
                if (response.data.success) {
                    $.toast('');
                } else {
                    $.alert(response.data.message);
                }
            }).catch(() => {
                $.hidePreloader();
            });
        }
    }
};

</script>



