<template>
<div class="page page-current">
    <header class="bar bar-nav">
        <router-link to="/home/buyer" class="button button-link button-nav pull-left"><span class="icon icon-left"></span></router-link>
        <h1 class="title">{{title}}</h1>
    </header>
    <div class="footer-submit">
        <div class="tips">
            <div class="main">
                 <span class="money"> <span class="unit"></span> {{commission||''}}</span>
            </div>
            <div class="sub">
                2
            </div>
        </div>
        <div class="submit">
            <a href="javascipt:;" @click.stop="submitFun"></a>
        </div>
    </div>
    <div class="content">
        <!--  -->
        <div class="description">
            <br> 
            <br> 1IP
            <br> 2
            <br> 3
            <br> 4500g
            <br> 5
        </div>
        <div class="main-box mt10">
            <div class="box-bd">
                <div class="list-block">
                    <ul class="thinForm">
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-title label"></div>
                                    <div class="item-input">
                                        <el-input v-model="form.storeName" placeholder="" autofocus></el-input>
                                    </div>
                                    <!-- <div class="newUser">300000</div> -->
                                </div>
                            </div>
                        </li>
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-title label"></div>
                                    <div class="item-input">
                                        <el-input v-model="form.goodsUrl" placeholder=""></el-input>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="main-box">
            <div class="box-hd">
                <span class="text"></span></div>
            <div class="box-bd">
                <div class="tmp-upload">
                    <ul class="tmp-list">
                        <li>
                            <div class="tips">
                                <span class="red">*</span> 
                            </div>
                            <div class="upload" @click="checkPhotoFun('tmp1')">
                                <span v-if="tmp1.uploadResult==='progress'" class="percent">{{tmp1.progress}}<i>%</i></span>
                                <img :src="tmp1.imageUrl" alt="">
                                <input type="file" ref="tmp1" name="multipartFiles" accept="image/*" @change="uploadPhotoFun('tmp1')">
                            </div>
                        </li>
                        <li>
                            <div class="tips">
                                <span class="red">*</span> 
                            </div>
                            <div class="upload" @click="checkPhotoFun('tmp2')">
                                <span v-if="tmp2.uploadResult==='progress'" class="percent">{{tmp2.progress}}<i>%</i></span>
                                <img :src="tmp2.imageUrl" alt="">
                                <input type="file" ref="tmp2" name="multipartFiles" accept="image/*" @change="uploadPhotoFun('tmp2')">
                            </div>
                        </li>
                        <li>
                            <div class="tips">
                                <span class="red">*</span> 
                            </div>
                            <div class="upload" @click="checkPhotoFun('tmp3')">
                                <span v-if="tmp3.uploadResult==='progress'" class="percent">{{tmp3.progress}}<i>%</i></span>
                                <img :src="tmp3.imageUrl" alt="">
                                <input type="file" ref="tmp3" name="multipartFiles" accept="image/*" @change="uploadPhotoFun('tmp3')">
                            </div>
                        </li>
                    </ul>
                    <div class="des">800*800</div>
                </div>
            </div>
        </div>
        <div class="main-box">
            <div class="box-bd">
                <div class="list-block">
                    <ul>
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-title label" style="width:7.5rem"> <span class="sub">()</span><span class="red">*</span></div>
                                    <div class="item-input">
                                        <el-input v-model="form.goodsPrice" placeholder=""></el-input>
                                    </div>
                                </div>
                            </div>
                        </li>
                        <li>
                            <div class="item-content">
                                <div class="item-inner noflex">
                                    <div class="item-title label"><span class="red">*</span></div>
                                    <div class="item-input">
                                        <el-input v-model="tags[0]" placeholder="1  "></el-input>
                                    </div>
                                    <div class="item-input">
                                        <el-input v-model="tags[1]" placeholder="2  "></el-input>
                                    </div>
                                    <div class="item-input">
                                        <el-input v-model="tags[2]" placeholder="3"></el-input>
                                    </div>
                                </div>
                            </div>
                        </li>
                        <li>
                            <div class="item-content">
                                <div class="item-inner noflex">
                                    <div class="item-title label"></div>
                                    <div class="item-input">
                                        <el-radio-group v-model="form.buyBackType">
                                            <el-radio :label="0"></el-radio>
                                            <el-radio :label="1">3-55</el-radio>
                                            <el-radio :label="2">6-106</el-radio>
                                        </el-radio-group>
                                    </div>
                                </div>
                            </div>
                        </li>
                        <li>
                            <div class="item-content">
                                <div class="item-inner noflex">
                                    <div class="item-title label" style="width:100%"></div>
                                    <div class="item-input">
                                        <el-radio-group v-model="form.needAlitm">
                                            <el-radio :label="0"></el-radio>
                                            <el-radio :label="1">1</el-radio>
                                        </el-radio-group>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <br>
    </div>
</div>
</template>
<script>
import upload from '@/javascript/upload';
let $ = window.$;
export default {
    name: 'homeCreate',
    data() {
        return {
            title: '',
            tags: [],
            commission: 0,
            form: {
                templateId: '',
                storeName: '',
                goodsUrl: '',
                goodsPicUrl: '',
                searchPicUrl: '',
                conditionPicUrl: '',
                goodsPrice: '',
                tags: '',
                buyBackType: 0,
                needAlitm: 0
            },
            tmp1: {
                imageUrl: '/static/image/tmp-bg.png',
                uploadResult: 'wait',
                progress: '0'
            },
            tmp2: {
                imageUrl: '/static/image/tmp-bg.png',
                uploadResult: 'wait',
                progress: '0'
            },
            tmp3: {
                imageUrl: '/static/image/tmp-bg.png',
                uploadResult: 'wait',
                progress: '0'
            }
        };
    },
    created() {
        if (this.$route.name === 'homeModify') {
            let id = this.$route.params.id;
            this.title = '';
            this.getDetailFun(id);
        }
    },
    watch: {
        'form.buyBackType': function (to, from) {
            console.log('to,from', to, from);
        }
    },
    methods: {
        getDetailFun(id) {
            this.$http.get('/tasktemplate/get', {
                params: {
                    templateId: id
                }
            }).then((response) => {
                if (response.data.success) {
                    let data = response.data.data.taskTemplate;
                    for (let i in this.form) {
                        switch (i) {
                            case 'tags':
                                this.form[i] = data[i];
                                if (data[i]) {
                                    this.tags = data[i].split(',');
                                }
                                break;
                            case 'goodsPicUrl':
                                this.form[i] = data[i] || '';
                                if (data[i]) {
                                    this.tmp1.imageUrl = data[i];
                                }
                                break;
                            case 'searchPicUrl':
                                this.form[i] = data[i] || '';
                                if (data[i]) {
                                    this.tmp2.imageUrl = data[i];
                                }
                                break;
                            case 'conditionPicUrl':
                                this.form[i] = data[i] || '';
                                if (data[i]) {
                                    this.tmp3.imageUrl = data[i];
                                }
                                break;
                            default:
                                this.form[i] = data[i];
                                break;
                        }
                    }
                }
            });
        },
        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-07
         * @lastTime    2018-06-07
         * @description 
         */
        checkPhotoFun(name) {
            if (this[name].uploadResult === 'wait') {
                this.$refs[name].dispatchEvent(new MouseEvent('click'));
            } else if (this[name].uploadResult === 'success' || this[name].uploadResult === 'progress') {
                // 
            } else {
                this.uploadPhotoFun(name);
            }
        },
        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-07
         * @lastTime    2018-06-07
         * @description 
         */
        uploadPhotoFun(name) {
            let _that = this;
            // 
            let file = this.$refs[name].files[0];
            // 
            this[name].imageUrl = URL.createObjectURL(file);
            // 
            upload({
                action: '/douyaer-api/file/upload',
                filename: 'multipartFiles',
                file: file,
                onSuccess(response) {
                    if (response.success) {
                        _that[name].uploadResult = 'success';
                        switch (name) {
                            case 'tmp1':
                                _that.form.goodsPicUrl = response.data.visitUrls[0].fileUrl;
                                break;
                            case 'tmp2':
                                _that.form.searchPicUrl = response.data.visitUrls[0].fileUrl;
                                break;
                            case 'tmp3':
                                _that.form.conditionPicUrl = response.data.visitUrls[0].fileUrl;
                                break;
                        }
                        $.toast('');
                    } else {
                        _that[name].uploadResult = 'fail';
                        $.toast(response.message);
                    }
                },
                onProgress(progress) {
                    _that[name].uploadResult = 'progress';
                    _that[name].progress = progress.percent;
                },
                onError(e) {
                    _that[name].uploadResult = 'fail';
                    $.toast('');
                }
            });
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-27
         * @lastTime    2018-06-27
         * @description 
         */
        submitFun() {
            let tags = [];
            for (var i = 0; i < this.tags.length; i++) {
                if (this.tags[i]) {
                    tags.push(this.tags[i]);
                }
            }
            this.form.tags = tags.join(',');

            let url;
            if (this.$route.name === 'homeModify') {
                url = '/tasktemplate/update';
            } else {
                url = '/tasktemplate/add';
            }

            $.showPreloader();
            this.$http.post(url, this.form).then((response) => {
                $.hidePreloader();
                if (response.data.success) {
                    $.toast('');
                    this.$router.replace({
                        path: '/home/buyer'
                    });
                } else {
                    $.alert(response.data.message);
                }
            }).catch(() => {
                $.hidePreloader();
            });
        }
    },
    components: {}
};
</script>



